.addnewpayment {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
}


.addnewpayment-form {
  /* min-width: 80%; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 60vw;
  flex-wrap: wrap;
  align-items: center;
  background-color: rgb(181, 222, 255);
  box-shadow: 2px 2px 2px grey;

}

.addnewpayment-form label {
  display: block;
  width: 50vw;
  font-size: .8rem;
  text-transform: uppercase;
}

.addnewpayment-form input,
.addnewpayment-form textarea {
  display: block;
  font-size: 1.1rem;

  width: 50vw;
}

.form-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 50vw;
  margin-top: 10px;
}

.form-row .form-amount {
  display: inline-block;
  width: 90px;
  text-align: right;
  margin-left: 10px;
}

.form-row .form-date {
  display: inline-block;
  width: 160px;
  margin-left: 10px;
  font-weight: bold;
  font-size: 1.2rem;
  padding: 0;
}

.form-row .form-label {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.form-btns {
  width: 30vw;
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
}

@media screen and (max-width: 800px) {

    .wrapper {
      background-color: #eee;
      height: 100vh;
    }
    .wrapper .columns {
      justify-content: start;
    }

    .addnewpayment-form {
      margin-top: 30px;
      width: 80vw;
    }

    .addnewpayment-form input,
    .addnewpayment-form textarea, 
    .addnewpayment-form label{
      font-size: 1rem;
      width: 70vw;
    }
    
    .addnewpayment-form label{
      font-size: 0.8rem;
    }

    .form-row {
      width: 70vw;
      display: block;
    }

    .form-row label {
      display: flex;
      justify-content: space-between;
      margin-bottom: 8px;
    }

    .form-row .form-date{
      text-align: right;
    }

    .form-isPaid select{
      font-size: 1rem;
    }
    .form-isPaid option{
      font-size: 0.7rem;
    }
}